<template>
    <div>
        <div class="df f-ac bc-ff0036 fixed" style="width:100%;top:0px;z-index:9">
            <div v-on:click="$router.back()" class="mintui mintui-back c-fff ml-10"></div>
            <div class="fg-3 p-5">
                <input type="text" class="search_ipt" :placeholder="placeholder" v-model="inputData">
            </div>
            <div class="fg-1 search_txt">
                <div>搜索</div>
            </div>

        </div>
        <div class="content">
            <div class="keyWordContent p-5 bc-fff">
                <div> 热门搜索</div>
                <keyWords v-for="(items,index) in keyWordsList" :keyWords="items"></keyWords>
            </div>
        </div>

    </div>

</template>
<script>
import keyWords from "../../components/keyWords";
//http://mall.jscntech.com//index.php?ctl=Index&met=getSearchKeyList&typ=json
export default {
    name: "goodsSearch",
    components: {
        keyWords
    },
    data() {
        return {
            inputData: "",
            placeholder: "",
            keyWordsList: []
        };
    },
    mounted: function() {
        this.keyWordsData();
    },
    methods: {
        keyWordsData() {
            //关键词 接口
            var _self = this;
            _self.common.get(
                "index.php?ctl=Index&met=getSearchKeyList&typ=json",
                {},
                function(data) {
                    console.log(data);
                    if (data) {
                        if (data.status == 200) {
                            var res = data.data.data;
                            _self.placeholder = res.default_list;
                            res.list.forEach(items => {
                                _self.keyWordsList.push(items);
                            });
                        }
                    }
                },
                function(error) {}
            );
        }
    }
};
</script>
<style scoped>
.mint-search {
    height: auto !important;
}
.keyWordContent span {
    display: inline-block;
    padding: 5px 10px;
    margin: 5px;
    border-radius: 10px;
    font-size: 13px;
    color: #303030;
    background-color: #fff;
    border: 1px solid #e2e2e2;
}
.search_ipt {
    width: 100%;
    border: none;
    padding: 8px 0px 8px 10px;
    border-radius: 10px;
}
.search_txt {
    color: #fff;
    font-size: 14px;
    text-align: center;
    padding-right: 5px;
}
</style>




