<template>
    <div class="bc-fff">
        <mt-header fixed title="商品评论">
            <div v-on:click="$router.back()" slot="left">
                <mt-button icon="back"></mt-button>
            </div>

        </mt-header>
        <div>

            <div class="products_reviews_content p-10" style="margin-top:46px;">
                <!-- 商品评论的类型选择 -->
                <div class="products_reviews_type">
                    <productReviewsType v-for="(items,index) in productReviewsTypeData" :num="count" :index="index" :typeStr="items.typeName" :type="items.type" v-on:type-product="activeProductType(index)"></productReviewsType>
                </div>
                <!-- 商品评论右侧弹出:评论列表 -->
                <productsReviewsList v-for="(items,index) in productsReviewsList" :nickname="items.nickname" :headerImg="items.headerImg" :details="items.details" :imgArr="items.imgArr" :data-type="items.type">
                </productsReviewsList>
            </div>
        </div>
    </div>

</template>
<script>
//商品详情列表的组件
import productsReviewsList from "../../components/products_reviews_list";
//商品评论的类型选择
import productReviewsType from "../../components/product_reviews_type";
export default {
    name: "goods_comment",
    components: {
        productsReviewsList,
        productReviewsType
    },
    data() {
        return {
            count: 0,
            productsReviewsList: [
                {
                    nickname: "YJSSNF--",
                    headerImg: require("../../assets/header.jpg"),
                    details:
                        "物流慢 等的很心急… 到货以后 完美 无瑕疵 正版无疑 就是游戏价格有些小贵了 没有其他淘宝店便宜… 一起买的价格竟然没有优惠… 其他都OK 值得入手 晚了一晚上"
                },
                {
                    nickname: "吃鸡吃鸡--",
                    headerImg: require("../../assets/header2.jpg"),
                    details:
                        "物流慢 等的很心急… 到货以后 完美 无瑕疵 正版无疑 就是游戏价格有些小贵了 没有其他淘宝店便宜… 一起买的价格竟然没有优惠… 其他都OK 值得入手 晚了一晚上",
                    imgArr: [
                        {
                            src: require("../../assets/imageContent.png")
                        },
                        {
                            src: require("../../assets/imageContent.png")
                        },
                        {
                            src: require("../../assets/imageContent.png")
                        }
                    ]
                },
                {
                    nickname: "YJSSNF--",
                    headerImg: require("../../assets/header.jpg"),
                    details:
                        "物流慢 等的很心急… 到货以后 完美 无瑕疵 正版无疑 就是游戏价格有些小贵了 没有其他淘宝店便宜… 一起买的价格竟然没有优惠… 其他都OK 值得入手 晚了一晚上"
                }
            ],
            productReviewsTypeData: [
                {
                    typeName: "全部",
                    type: 0
                },
                {
                    typeName: "质量不错(64)",
                    type: 1
                },
                {
                    typeName: "快递不错(51)",
                    type: 1
                },
                {
                    typeName: "态度不错(50)",
                    type: 1
                },
                {
                    typeName: "是正品(40)",
                    type: 1
                },
                {
                    typeName: "性能好(6)",
                    type: 1
                },
                {
                    typeName: "物流太慢(18)",
                    type: 0
                }
            ]
        };
    },
    methods: {
        activeProductType(index) {
            //count需要传递给子组件
            //:num
            this.count = index;
            //调用自己的公用方法
            //this.common
            this.common.openIndicator();
            setTimeout(() => {
                this.common.closeIndicator();
            }, 2000);
        }
    }
};
</script>
<style scoped>
</style>



