<template>
    <div class="product">
        <div class="listContent" :data-id="id" @click="productDetails(id)">
            <div class="plist">
                <img :src="imageSrc" alt="">
                <span class="productTitle">{{title}}</span>
            </div>

        </div>

    </div>
</template>
<script>
export default {
    //自定义的组件
    name: "productList",
    props:{
        //props传递组件的参数
        title:{
            type:String,
            default:"商品标题"
        },
        imageSrc:{
            type:String,
            default:require("../assets/default.png")
        },
        id:{
            type:String,
            default:"0"
        }
    },
    //自定义事件
    methods:{
        productDetails(id){
            // 匹配监听
            this.$emit('my-event',id);
        }
    }
    
};
</script>
<style scoped>
.plist{
   background-color: #fff;
}
.listContent {
    width: 50%;
    float: left;
}
.listContent .plist {
    padding: 10px;
}
.product img {
    display: block;
    width: 100%;
}
.productTitle {
    font-size: 13px;
    color: #303030;
    text-align: center;
    display: block;
    padding: 10px 0px;
    flex-wrap: wrap;
}
</style>



