<template>
    <!-- <div>
        <home></home>
    </div> -->
    <div>
        <router-view/>
        <footer class="df">

            <div v-for="(items,index) in tabsItems" class="fg-1" v-on:click="tabs(index,items.path)">
                <div v-bind:class="{'c-ff0036':current == index}">
                    <div class="fs-27" v-bind:class="items.className"></div>
                </div>
                <div class="fs-13" v-bind:class="{'c-ff0036':current == index}">{{items.title}}</div>
            </div>

        </footer>

    </div>

</template>
<script type="text/ecmascript-6">
export default {
    name: "tab",
    data() {
        return {
            current: 0,
            imgActive: false,
            tabsItems: [
                {
                    title: "首页",
                    path: "/home",
                    className: "icon-homePage",
                    ids: "homePage",
                    src: require("../assets/homepage.png"),
                    active_src: require("../assets/homepage_fill.png")
                },
                {
                    title: "社交",
                    path: "/social",
                    className: "icon-shejiaoPage",
                    ids: "shejiaoPage",
                    src: require("../assets/order.png"),
                    active_src: require("../assets/order_fill.png")
                },
                {
                    title: "电影",
                    path: "/film",
                    className: "icon-seePage",
                    ids: "filmPage",
                    src: require("../assets/browse.png"),
                    active_src: require("../assets/browse_fill.png")
                },
                {
                    title: "我的",
                    path: "/Main",
                    className: "icon-mainPage",
                    ids: "mainPage",
                    src: require("../assets/group.png"),
                    active_src: require("../assets/group_fill.png")
                }
            ]
        };
    },
    methods: {
        tabs(idx, path) {
            this.$router.push({
                path: path,
                query: {
                    idx: idx
                }
            });
            this.current = idx;
        },
        thisRoute() {
            if(!this.$route.query.idx || this.$route.query.idx == ""){
                this.$route.query.idx = 0;
            }
            this.current = this.$route.query.idx
        }
    },
    created() {
        this.thisRoute();
    },
    watch: {
        selected: function(val, oldVal) {}
    }
};
</script>
<style scoped>
footer {
    position: fixed;
    width: 100%;
    bottom: 0px;
    text-align: center;
    color: #303030;
    background-color: #fff;
}
</style>



